@use '../../styles/breakpoint.scss';

@font-face {
  font-family: 'DFHeiStd-W9';
  src: url('../../assets/fonts/DFHeiStd-W9.otf');
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
}

@font-face {
  font-family: 'DFHeiStd-W7';
  src: url('../../assets/fonts/DFHeiStd-W7.otf');
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FuturaCom';
  src: url('../../assets/fonts/FuturaCom.ttf');
  font-weight: bold;
  font-stretch: condensed;
  font-style: normal;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background-color: #012674;

  .buttons {
    display: flex;
    flex-direction: row;
    margin-top: 786px;
    
    button {
      padding: 19px 96px 21px;
      border-radius: 37.5px;
      border: none;
      background-color: #db0840;
      cursor: pointer;

      p {
        font-family: 'DFHeiStd-W9';
        font-size: 35px;
        line-height: 1.46;
        letter-spacing: 1.05px;
        text-align: center;
        color: #fff;
        margin: 0;
      }
    }

    button:last-child {
      margin-left: 35px;
    }
  }

  .title {
    font-family: 'DFHeiStd-W9';
    font-size: 64px;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
    margin: 72.5px 0px 23px 0px;
  }

  .subtitle {
    font-family: 'DFHeiStd-W9';
    font-size: 36px;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
    display: flex;
    flex-direction: row;

    p{
      font-family: 'FuturaCom';
      font-size: 36px;
      margin: 0 12px;
    }
  }

  .search {
    display: flex;
    align-items: center;
    margin: 56.5px 0px 72.5px 0px;
    padding: 10px 18px;
    border-radius: 37.5px;
    border: solid 3px #fff;
    width: 580px;
    height: 75px;

    svg {
      width: 54px;
      height: 54px;
      margin-right: 8px;
      fill: #fff;

      path {
        fill: #fff;
      }
    }

    input {
      font-family: 'DFHeiStd-W9';
      font-size: 36px;
      line-height: normal;
      letter-spacing: normal;
      color: #fff;
      background-color: transparent;
      border: none;
    }

    input:focus {
      outline: none;
    }
  }

  .table{
    margin-bottom: 55px;
    width: 664px;

    .header {
      background-image: url('../../assets/images/table_header.png');
      background-size: cover;
      height: 77px;
      display: flex;
      flex-direction: row;
      align-items: center;

      .text {
        font-family: 'DFHeiStd-W7';
        font-size: 34px;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
        color: #fff;
      }

      .rank {
        flex: 1.5;
        @extend .text;
      }

      .id {
        flex: 3;
        @extend .text;
      }

      .score {
        flex: 2;
        @extend .text;
        width: 214px;
      }
    }

    .body {
      box-shadow: 20px 3px 6px 0 rgba(0, 0, 0, 0.1);
      border: solid 4px #fff;

      .cell {
        height: 82px;
        display: flex;
        flex-direction: row;
        align-items: center;
        background-image: linear-gradient(to right, rgba(255, 255, 255, 0.46) 50%, transparent 50%);
        background-size: 10px 1px;
        background-repeat: repeat-x;

        .text {
          height: 82px;
          font-family: 'FuturaCom';
          font-size: 34px;
          line-height: 2.12;
          letter-spacing: normal;
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
        }
  
        .rank {
          flex: 1.5;
          @extend .text;
          border-right: solid 2px #fff;
        }
  
        .id {
          flex: 3;
          @extend .text;
          border-right: solid 2px #fff;
        }
  
        .score {
          flex: 2;
          @extend .text;
          width: 214px;
        }
      }

      .cell:first-child {
        background-image: none;
      } 
    }
  }

  .bottomLogo {
    width: 391px;
    height: 284px;
    object-fit: cover;
    margin-bottom: 138px;
  }
}

@media (max-width: breakpoint.$mobile){}